html {
  font-size: clamp(14px, 1vw + 10px, 18px);
}
:root {
  --green: #719D54;
  --teal: #4F7477;
  --gray: #545656;
  --bg: #F4F7F5;
  --card-bg: #fff;
  --critical: #D4483B;
  --critical-border: #E6B5B0;
  --sidebar-bg: darkslategray;
  --active-bg: #72a65d;
  --hover-bg: lightgrey;
}
:root[data-theme="accessible"] {
  --green: #4A9F70;
  --teal: #1B5E68;
  --sidebar-bg: #0F4C5C;
  --active-bg: #2C9AB7;
  --critical: #C4473A;
  --critical-border: #DC9A87;
  --hover-bg: #C8D8E0;
}
body {
  margin: 0;
  background: var(--bg);
  font-family: Arial, sans-serif;
  color: var(--gray);
  display: flex;
  height: 100%;
}
* {
  transition: 0.25s ease;
}
.sidebar {
  width: 240px;
  background: #2f4e4f;
  min-height: 100vh;
  padding: 32px 22px;
  display: flex;
  flex-direction: column;
  gap: 28px;
  transition: width .3s ease;
}
.sidebar.collapsed {
  width: 80px;
}
.nav-menu {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: flex-start;
}
.icon-normal svg {
  width: 22px;
  height: 22px;
}
.icon-large svg {
  width: 20px;
  height: 20px;
}
.sidebar-item {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  padding: 12px 14px;
  cursor: pointer;
  color: #dfece1;
  font-weight: 600;
  border-radius: 10px;
  transition: all .25s ease;
  background: transparent;
}
.sidebar-item:hover {
  background: rgba(255,255,255,.10);
  transform: translateX(5px);
}
.sidebar-item.active {
  background: none;
  border-left: 4px solid var(--green);
  padding-left: 20px;
  color: var(--green);
}
.sidebar-item.active svg {
  stroke: var(--green);
}
.sidebar.collapsed .sidebar-item active .sidebar-item span {
  display: none;
}
.sidebar.collapsed .sidebar-item {
  padding-right: 35px;
  justify-content: center;
}
.collapse-btn {
  position: absolute;
  top: 300px;
  right: 25px;
  border-radius: 8px;
  padding: 6px 9px;
  color: white;
  border: none;
  cursor: pointer;
}
.collapse-btn:hover {
  transform: scale(1.12);
}
.main-wrapper {
  flex: 1;
  padding: 40px;
}
.topbar {
  background: var(--teal);
  padding: 14px 24px;
  border-radius: 12px;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 3px 12px rgba(0,0,0,0.15);
}
.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 22px;
  margin-top: 28px;
}
.metric-card {
  background: #FFFFFF;
  border-radius: 14px;
  padding: 22px 26px;
  border: 1px solid #D5DEC7;
  box-shadow: 0 3px 12px rgba(0,0,0,0.08);
  display: flex;
  flex-direction: column;
  gap: 6px;
  transition: .25s ease;
}
.metric-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 18px rgba(0,0,0,0.15);
}
.metric-value {
  font-size: 34px;
  font-weight: 800;
  color: #2D3E1E;
}
.metric-label {
  font-size: 14px;
  font-weight: 600;
  color: #546243;
